import { Component, OnInit,ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup,FormControl,ValidatorFn,AbstractControl } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { UserService } from 'app/shared/services/user/user.service';
import { DatePipe } from '@angular/common';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import {CountriesService} from 'app/shared/services/countries.service';
import {MessageConstant} from 'app/shared/utils/message.constant';
import { ClientService } from 'app/shared/services/client/client.service';
import { RoleService } from 'app/shared/services/user/role.service';
import { CpoService } from 'app/shared/services/cpo/cpo.service';
import { ChargingStationService } from 'app/shared/services/charging-station/charging-station.service';
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
} 
@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations,
  providers:[DatePipe]
})
export class CreateComponent implements OnInit {
 
  isPasswordFieldType: boolean;
  isPasswordConfirmFieldType: boolean;
  form: FormGroup;
  localStorage : any;
  params: any;
  role_types:any;
  filteredOptionsRole: Observable<any[]>;
  filteredOptionsCpo: Observable<any[]>;
  filteredOptionsClient: Observable<any[]>;
  filteredOptionCountry: Observable<any[]>;
  filteredOptionState: Observable<any[]>;
  filteredOptionCity: Observable<any[]>;
  filteredOptionsCharStat: Observable<any[]>;
  countries:any;
  stateName:any;
  cities:any;
  states:any;
  pin_code_id:any;
  country_id:any;
  state_id:any;
  cpo_types:any;
  clients:any;
  status = true;
  errorMsg:String;
  splitted:String;
  chargingStationName: any;
  isSubmit=true;
  @ViewChild("activeElement") active:any;
  maxDate=new Date();
  stationValid:boolean  =false;
  cpoValid:boolean = false;
  selectedClient:any;
  constructor(private fb: FormBuilder, private masterService : MasterService,
    private userService : UserService,private snackBar: MatSnackBar,private chargingStationService: ChargingStationService,
    private router : Router,private datePipe: DatePipe, private clientService : ClientService,
    public countryService: CountriesService,private roleService:RoleService,private cpoService:CpoService) {
     
    
  }

  ngOnInit() {
    this.initialize();
    
  }

  initialize(){
   
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      client: ['', [autocompleteObjectValidator(),Validators.required]],
      roleType: ['', [autocompleteObjectValidator(),Validators.required]],
      cpoType:['', [autocompleteObjectValidator(), Validators.required]],
      charStat: ['', [autocompleteObjectValidator(), Validators.required]],
      f_name: ['', [Validators.pattern(/^[a-zA-Z]*$/),Validators.required]],
      m_name: ['' ,[Validators.pattern(/^[a-zA-Z]*$/)]],
      l_name: ['', [Validators.pattern(/^[a-zA-Z]*$/),Validators.required]],
      pan: ['', [Validators.pattern(/^[a-zA-Z0-9]*$/)]],
      u_empCode: ['', [Validators.pattern(/^[a-zA-Z0-9]*$/)]],
      u_dob: ['', [Validators.required]],
      u_email: ['', [Validators.required,Validators.pattern('^[^\\s@]+@[^\\s@]+\\.[^\\s@]{2,}$')]],
      u_mobile: ['', [Validators.required ,Validators.pattern('^((\\+91-?)|0)?[0-9]{10}$')]],
      u_altmobile: ['', [Validators.pattern('^((\\+91-?)|0)?[0-9]{10}$')]],
      u_userID: ['', [Validators.pattern(/^[a-zA-Z0-9._]*$/),Validators.required]],
      u_aadhar: [''],
	    u_password: ['', [Validators.required,Validators.pattern('^[a-zA-Z0-9](?=.*?[_#?!@$%^&*-]).{6,}$')]],
      u_confirmPassword: ['', [this.confirmValidator]],
      u_addressline1: ['', [Validators.required]],
      u_addressline2: [''],
      pincode: ['', [Validators.pattern('^[1-9]{1}[0-9]{2}\\s{0,1}[0-9]{3}$'),Validators.required]],
      u_landmark: [''],
      country: ['', [autocompleteObjectValidator(),Validators.required]],
      state: ['', [autocompleteObjectValidator(),Validators.required]],
      city: ['', [Validators.required]],
      status : this.status == true ? 'Y' : 'N'
    });
   //this.filteredOptions();
   
   this.getAllCountry();
    this.getAllState();
    this.getClients();
   this.getRoleTypes(this.localStorage.project_id,this.localStorage.client_id);
   //this.getCpoTypes(this.localStorage.client_id);
   
  }
  
  getRoleTypes(project_id,client_id){
    this.roleService.getActiveRolesByClientId(project_id,client_id).subscribe(
      (resp:any)=>{
        this.role_types = resp.data;
        this.filteredOptionsRole = this.form.controls.roleType.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterRoleType(name) : this.role_types)
          );
         },(err)=>{
           
         }
    )
    //this.role_types = this.userService.getRoleTypes();
  }
  onRoleChange(object){
    //console.log(object);
    if(object.code == 'ADMIN'){

      this.cpoValid = false;
      this.stationValid = false;
      this.form.get('cpoType').setValue(null);
      this.form.get('charStat').setValue(null);
      this.form.get('cpoType').clearValidators();
      this.form.get('charStat').clearValidators();
    }
    else if(object.code == 'EU'){
      this.cpoValid = false;
      this.stationValid = false;
      this.form.get('cpoType').setValue('');
      this.form.get('charStat').setValue('');
      this.form.get('cpoType').clearValidators();
      this.form.get('charStat').clearValidators();
    }
    else if(object.code == 'CPO'){
      this.cpoValid = true;
      this.form.get('charStat').setValue('');
      this.form.get('charStat').clearValidators();
      this.stationValid = false;
    }
    else if(object.code == 'SC'){
      this.cpoValid = true;
      this.stationValid = true;
    }
   else if(object.code == 'SO'){
      this.cpoValid = true;
      
      this.stationValid = true;
   }
   else{
    this.form.get('cpoType').clearValidators();
    this.form.get('charStat').clearValidators();
      this.form.get('cpoType').setValue('');
      this.form.get('charStat').setValue('');
        this.cpoValid = false;
        this.stationValid = false;
    }
    
    this.form.get("cpoType").setValue('');
    this.form.get("charStat").setValue('');
    this.getCpoTypes(this.selectedClient);
  }

  getAllCountry(){
    this.countryService.getAllCountry().subscribe(
      (data:any)=>{
        this.countries = data;
         this.filteredOptionCountry = this.form.controls.country.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterCountry(name) : this.countries)
           );
         },(err)=>{
           
         }
    )
  }
  getAllState(){
    this.countryService.getAllState().subscribe(
      (stateData:any)=>{
        this.states = stateData;
         this.filteredOptionState = this.form.controls.state.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterState(name) : this.states)
           );
         },(err)=>{
           
         }
    )
  }
  
  getAllCity(){
    this.countryService.getAllCities().subscribe(
      (resp:any)=>{
        this.cities = resp.data;
         this.filteredOptionCity = this.form.controls.city.valueChanges
           .pipe(
             startWith(''),
             map(value => typeof value === 'string' ? value : value.name),
             map(name => name ? this._filterCity(name) : this.cities)
           );
         },(err)=>{
           
         }
    )
  }
  
  displayRole(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterRoleType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.role_types.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  
  confirmValidator = (control: FormControl): { [s: string]: boolean } => {
    if (!control.value) {
      return { error: true, required: true };
    } else if (control.value !== this.form.controls.u_password.value) {
      return { error: true, confirm: true };
    }
  }
  togglePasswordFieldType(){
    this.isPasswordFieldType = !this.isPasswordFieldType;
  }
  togglePasswordConfirmFieldType(){
    this.isPasswordConfirmFieldType = !this.isPasswordConfirmFieldType;
  }
  onSubmit(){

    let formValues = this.form.value;
    if(formValues.roleType.code == "CPO"){
      this.form.get('cpoType').setValidators([autocompleteObjectValidator(),Validators.required])
    }
    if(formValues.roleType.code == "SC"){
      this.form.get('cpoType').setValidators([autocompleteObjectValidator(),Validators.required])
      this.form.get('charStat').setValidators([autocompleteObjectValidator(),Validators.required])
    }
    if(formValues.roleType.code == "SO"){
      this.form.get('cpoType').setValidators([autocompleteObjectValidator(),Validators.required])
      this.form.get('charStat').setValidators([autocompleteObjectValidator(),Validators.required])
    }
    if (this.form.invalid){
        return;
    }else{
      let params = {
        client_id:formValues.client.id,
        cpo_id: formValues.cpoType == null ? 0  : formValues.cpoType.id,
        char_Station: formValues.charStat == null ? 0  : formValues.charStat.id,
        user_type:formValues.roleType.code,
        f_Name: formValues.f_name,
        role_id:formValues.roleType.id,
        m_Name: formValues.m_name,
        l_Name: formValues.l_name,
        PAN: formValues.pan,
        employee_code: formValues.u_empCode,
        dob: this.datePipe.transform(formValues.u_dob, "yyyy-MM-dd"),
        email : formValues.u_email,
        mobile : formValues.u_mobile,
        alt_mobile: formValues.u_altmobile,
        username : formValues.u_userID,
        aadhar : formValues.u_aadhar,
        password: formValues.u_confirmPassword,
        address1: formValues.u_addressline1,
        address2: formValues.u_addressline2,
        PIN: formValues.pincode,
        landmark: formValues.u_landmark,
        country_id: formValues.country.id,
        state_id: formValues.state.id,
        city_id: formValues.city.id,
        can_expire:"Y",
        status :  this.status == true ? 'Y' : 'N',
        registration_origin:"WEB",
        created_by : this.localStorage.userId 
      }
      
     
      
  
      
      this.userService.create(params).subscribe(
        (resp:any )=>{
          if(resp.status == true){
            this.snackBar.open(MessageConstant.userCreated, 'Dismiss', { duration: 5000 });
            this.router.navigate(['/userManagement/user/list']);
          }else{
            if(resp.err_code == "ERROR : ER_DUP_ENTRY"){
              this. errorMsg = resp.message;
              var index=  this. errorMsg.lastIndexOf(" ");
              this.splitted = this. errorMsg.substring(index+1); 
              if(this.splitted == "'username'" ){
                this.snackBar.open(MessageConstant.userDuplicate, 'Dismiss', { duration: 5000 });
              }if(this.splitted == "'mobile'"){
                this.snackBar.open(MessageConstant.mobileDuplicateUser, 'Dismiss', { duration: 5000 });
              }if(this.splitted == "'email'"){
                this.snackBar.open(MessageConstant.emailDuplicateUser, 'Dismiss', { duration: 5000 });
              }
              if(this.splitted == "'PAN'"){
                this.snackBar.open(MessageConstant.panDuplicateUser, 'Dismiss', { duration: 5000 });
              }
            }else{
              this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
            }
            //this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
          }
  
        },
        (err)=>{
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
        }
      )
    }
   

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }
  updateStatus(event){
    this.status = event.checked;
  
  }
  getStateCountry(){
    
    this.pin_code_id = this.form.value.pincode;
  
  //;
  this.countryService.getCountryStateByPIN(this.pin_code_id ).subscribe(
    (data:any)=>{
      //this.form.reset();
      if(data != undefined && data != null && data.length > 0){
        this.state_id = data[0].state_id;
        //;
        this.countryService.getCountryByState(this.state_id ).subscribe(
          (countrydata:any)=>{
            //this.form.reset();
            this.countries = countrydata;
            const toSelect = this.countries.find(c => c.id == data[0].country_id);
            this.form.get('country').setValue(toSelect);
            
            this.filteredOptionCountry = this.form.controls.country.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterCountry(name) : this.countries)
            );
            //; 
            this.country_id = data[0].country_id;
            this.countryService.getStateByCountry(this.country_id ).subscribe(
              (statedata:any)=>{
                //this.form.reset();
                this.states = statedata;
                const toSelect = this.states.find(c => c.id == data[0].state_id);
                this.form.get('state').setValue(toSelect);
                
                this.filteredOptionState = this.form.controls.state.valueChanges
                .pipe(
                  startWith(''),
                  map(value => typeof value === 'string' ? value : value.name),
                  map(name => name ? this._filterState(name) : this.states)
                );
                
              },(err)=>{
                
              }
            )
            //; 
            this.state_id = data[0].state_id;
            this.countryService.getCityByState(this.state_id ).subscribe(
              (Citydata:any)=>{
                //this.form.reset();
                this.cities = Citydata;
                //const toSelect = this.states.find(c => c.id == data[0].state_id);
               // this.form.get('state').setValue(toSelect);
                
                this.filteredOptionCity = this.form.controls.city.valueChanges
                .pipe(
                  startWith(''),
                  map(value => typeof value === 'string' ? value : value.name),
                  map(name => name ? this._filterCity(name) : this.cities)
                );
                
              },(err)=>{
                
              }
            )
          },(err)=>{
            
          }
        )
      }else{

      }
     
      
    },(err)=>{
      
    }
    )
  }
  onCountryChange(event){
    this.countries = [];
    this.getAllCountry();
    this.getStatesByCountry(event);
    
   
    //this.form.get('pincode').setValue( "");
    this.form.get('state').setValue( "");
    this.form.get('city').setValue( "");
  }
  getStatesByCountry(state){
     this.state_id = state.id;
     this.countryService.getStateByCountry(this.state_id ).subscribe(
      (statedata:any)=>{
        //this.form.reset();
        this.states = statedata;
       // const toSelect = this.states.find(c => c.id == data[0].state_id);
       // this.form.get('state').setValue(toSelect);
        
        this.filteredOptionState = this.form.controls.state.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterState(name) : this.states)
        );
        
      },(err)=>{
        
      }
    )
  }
  onStateChange(event){
    this.getCitiesByStates(event);
    this.getCountryByState(event);
    this.form.get('city').setValue( "");
    //this.form.get('pincode').setValue( "");
  }
  onCityChange(event){
   
    //this.form.get('pincode').setValue( "");
  }
  getCitiesByStates(stateId){
    this.state_id = stateId.id;
     this.countryService.getCityByState(this.state_id ).subscribe(
      (citiesdata:any)=>{
        //this.form.reset();
        this.cities = citiesdata;
       // const toSelect = this.states.find(c => c.id == data[0].state_id);
       // this.form.get('state').setValue(toSelect);
        
        this.filteredOptionCity = this.form.controls.city.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCity(name) : this.cities)
        );
        
      },(err)=>{
        
      }
    )
  }
  getCountryByState(stateId){
    this.state_id = stateId.id;
    this.countryService.getCountryByState(this.state_id ).subscribe(
      (countrydata:any)=>{
        //this.form.reset();
        this.countries = countrydata;
        const toSelect = this.countries.find(c => c.id == countrydata[0].id);
        this.form.get('country').setValue(toSelect);
        
        this.filteredOptionCountry = this.form.controls.country.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCountry(name) : this.countries)
        );
       
     },(err)=>{
       
     }
   )
  }
  displayCountry(item: any): string {
  return item && item.name ? item.name : '';
  }
  private _filterCountry(name: string): any[] {
  const filterValue = name.toLowerCase();
  return this.countries.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayState(item: any): string {
   return item && item.name ? item.name : '';
  }
  private _filterState(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.states.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayCity(item: any): string {
   return item && item.name ? item.name : '';
  }
  private _filterCity(name: string): any[] {
    const filterValue = name.toLowerCase();
   return this.cities.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  getCpoTypes(client_id){
    
    //this.cpo_types = this.userService.getCPOTypes();
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    this.cpoService.getCpoByClientId(client_id).subscribe(
      (data:any)=>{
        this.cpo_types = data;
        this.filteredOptionsCpo = this.form.controls.cpoType.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterCpoType(name) : this.cpo_types)
        );
      },(err)=>{
        
      }
    )
  }

  onCPOChange(event) {
    this.form.get('charStat').setValue("");
    this.getStationByCPO(event);
  }

  displayCpo(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCpoType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.cpo_types.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  getClients(){
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
      (data:any)=>{
        this.clients = data;

         const toSelect = this.clients.find(c => c.id == this.localStorage.client_id);
         this.form.get('client').setValue(toSelect);
         this.selectedClient = toSelect.id;

        this.filteredOptionsClient = this.form.controls.client.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterClientModels(name) : this.clients)
        );
      },(err)=>{
        
      }
    )
  }
  displayClient(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterClientModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.clients.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  onClientChange(value){
    this.getRoleTypes(this.localStorage.project_id,value.id);
    this.selectedClient = value.id;
    this.getCpoTypes(value.id)
    this.form.get('cpoType').setValue("");
    this.form.get('roleType').setValue('');
    this.form.get('charStat').setValue('');
  }

  getStationByCPO(id) {
    this.chargingStationService.getChargingStationByCpoId(id).subscribe(
      (data: any) => {
        this.chargingStationName = data.data;
        this.filteredOptionsCharStat = this.form.controls.charStat.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterCharStat(name) : this.chargingStationName)
          );
      },
      (err) => {
        ////
      })
  }
  displayCharStat(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterCharStat(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.chargingStationName.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  
}


